import SVG from 'svg.js';

const helloEllipse = SVG('hello-ellipse').size('100%', '100%');
const libraryEllipse = SVG('library-ellipse').size('100%', '100%');
const recipesEllipse = SVG('recipes-ellipse').size('100%', '100%');
const dietEllipse = SVG('diet-ellipse').size('100%', '100%');
const gymEllipse = SVG('gym-ellipse').size('100%', '100%');
const eduEllipse = SVG('edu-ellipse').size('100%', '100%');
const eduEllipse2 = SVG('edu-ellipse-2').size('100%', '100%');
const newsEllipse = SVG('news-ellipse').size('100%', '100%');

const leftEllipses = [libraryEllipse, dietEllipse];
const rightEllipses = [recipesEllipse, gymEllipse, newsEllipse];

const createGradient = (draw) => {
    return draw.gradient('linear', (stop) => {
            stop.at(0, '#31e050')
            stop.at(1, '#6cc9ef')
        })
        .from(0, 0).to(0, 1);
}

const createRightEllipse = (draw) => {
    const grad = createGradient(draw);
    draw.ellipse('100%', '100%').fill(grad).rx('80%')
        .center('100%', '50%');
}

const createLeftEllipse = (draw) => {
    const grad = createGradient(draw);
    draw.ellipse('170%', '100%').fill(grad)
        .center(0, '50%');
}

const createTopEllipse = (draw) => {
    const grad = createGradient(draw);
    draw.ellipse('100%', '100%').fill(grad).ry('100%').rx('90%')
        .center('50%', 0);
}

const createBottomEllipse = (draw) => {
    const grad = draw.gradient('linear', (stop) => {
        stop.at(0, '#31e050')
        stop.at(.8, '#6cc9ef')
    })
    .from(0, 0).to(0, 1);
    draw.ellipse('130%', '120%').fill(grad)
        .center('50%', '80%');
}
const createCustomEllipse = (draw, cx, cy, rx, ry) => {
    const grad = draw.gradient('linear', (stop) => {
        stop.at(0.4, '#6cc9ef')
        stop.at(1, '#31e050')
    })
    .from(0,0).to(0,1);

    draw.ellipse(rx, ry).fill(grad)
    .center(cx, cy);
}

createTopEllipse(helloEllipse);
createBottomEllipse(eduEllipse);
createCustomEllipse(eduEllipse2, '50%', 0, '130%', '140%');
rightEllipses.forEach(el => createRightEllipse(el));
leftEllipses.forEach(el => createLeftEllipse(el));